.form {
  margin-top: 35px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.input {
  font-family: "Montserrat";
  font-weight: 500;
  outline: none !important;
  width: 100%;
  height: 35px;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 2px 15px;
}

.input:focus {
  border: solid #339EDA 2px;
}

::placeholder {
  font-family: "Montserrat";
  font-weight: normal;
  font-size: 14px;
}

.submitButton {
  font-family: "Montserrat";
  font-weight: 500;
  cursor: pointer;
  width: 100%;
  height: 35px;
  border-radius: 5px;
  opacity: 0.8;
  background-color: darkgrey;
}

.submitButton:hover {
  opacity: 1;
}

.sessionText {
  margin-top: 20px;
  margin-left: 20px;
}

.loginError {
  margin-top: 10px;
  color: red;
}