.container {
  margin-top: 45px;
}

.list {
  cursor: pointer;
  width: 100%;
  height: 50px;
  border: grey 1px solid;
  padding: 2px 20px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  transition: background ease 0.1s;
}

.list:hover {
  background: #b1b1b1;
}

.text {
  margin: 0;
}

.addButton {
  cursor: pointer;
  margin: 0;
  margin-top: 45px;
  width: 200px;
  height: 50px;
  line-height: 50px;
  border: black 2px solid;
  border-radius: 5px;
  text-align: center;
}

.addButton:hover {
  background: #78d4f0;
}